.jumbotron > .overlay {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: rgba(108, 122, 137, 0.55);
  z-index: 1;
}

.jumbotron > .inner {
  position: relative;
  z-index: 2;
}

